form {
    width: 100%;
    display: grid;
    grid-template-columns: 3fr, 2fr, 2fr, 1fr;
    grid-template-areas: 
        "description amount select btn";
    grid-gap: 20px;
    margin: 10px 0;
}

input,
input::-webkit-input-placeholder,
select,
.add-btn {
    padding: 10px;
    color: var(--text-color);
    font-family: var(--font-family);
    font-size: 18px;
    text-align: center;
}

input,
input:-ms-input-placeholder,
select,
.add-btn {
    padding: 10px;
    color: var(--text-color);
    font-family: var(--font-family);
    font-size: 18px;
    text-align: center;
}

input,
input::-ms-input-placeholder,
select,
.add-btn {
    padding: 10px;
    color: var(--text-color);
    font-family: var(--font-family);
    font-size: 18px;
    text-align: center;
}

input,
input::placeholder,
select,
.add-btn {
    padding: 10px;
    color: var(--text-color);
    font-family: var(--font-family);
    font-size: 18px;
    text-align: center;
}

input::-webkit-input-placeholder {
    font-style: italic;
}

input:-ms-input-placeholder {
    font-style: italic;
}

input::-ms-input-placeholder {
    font-style: italic;
}

input::placeholder {
    font-style: italic;
}

.description {
    grid-area: description;
}

.amount {
    grid-area: amount;
}

select {
    grid-area: select;
}

.add-btn {
    -webkit-box-shadow: var(--box-shadow);
            box-shadow: var(--box-shadow);
    grid-area: btn;
    border-radius: 5px;
}

@media screen and (max-width: 800px) {
    form {
        grid-template-columns: 100%;
        grid-template-areas: 
            "description"
            "amount" 
            "select" 
            "btn";
    }
}


